import { Component } from '@angular/core';
import { NgForm, FormBuilder, FormControl, Validators, FormGroup } from '@angular/forms';
import { HttpService } from "../app/httpService.service";
import Typed from 'typed.js';
import { NotifierService } from "angular-notifier";
import { NgxUiLoaderService } from 'ngx-ui-loader';
declare var swal: any;

// import * as $ from 'jquery';
@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css']
})
export class AppComponent {

    constructor(private notifierService: NotifierService, private fb: FormBuilder, public http: HttpService, private spinner: NgxUiLoaderService) { }
    title = 'Portfolio';
    loader: boolean = false;
    FormData: FormGroup = this.fb.group({
        fullName: ['', [Validators.required]],
        email: ['', [Validators.compose([Validators.required, Validators.email])]],
        mobile: ['', [Validators.required,  Validators.pattern("^((\\+91-?)|0)?[0-9]{10}$")]],
        comment: ['', [Validators.required]],
    });

    get f(){  
        return this.FormData.controls;  
      }  
    Send(FormData: any) {
        this.spinner.start();
        if(FormData.status=="VALID"){
        
        console.log("yes works submit", FormData)
        
        this.http.sendEmail("http://localhost:3000/sendmail", FormData.value).subscribe(
            data => {
                let res: any = data;
                this.spinner.stop();
                swal.fire({
                    html:
                        'Your mail and message has been sent to <b>Rajat</b> ',
                    showConfirmButton: false,
                    imageUrl: 'assets/images/thanks.PNG',
                    imageWidth: 400,
                    imageHeight: 200,
                    imageAlt: 'Custom image',

                });
                console.log(
                    `ðŸ‘ > ðŸ‘ > ðŸ‘ > ðŸ‘ ${FormData.value.name} is successfully register and mail has been sent and the message id is ${res.messageId}`
                );
            },
            err => {
                console.log(err);
                this.spinner.stop();


                swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: 'Something went wrong!',
                    showConfirmButton: false,
                    showCancelButton: true,
                    cancelButtonColor: '#d33',
                })

            }, () => {
                // this.loading = false;
                // this.buttionText = "Submit";
            }
        );
        }
        else{
            this.spinner.stop();
            swal.fire({
                icon: 'info',
                text: 'Please enter all fields',
                showCancelButton: false,
                cancelButtonColor: '#d33',
            })
        }
    }

    ngOnInIt() {
        $(document).ready(function () {
            $(window).scroll(function () {
                // sticky navbar on scroll script
                if (scrollY > 20) {
                    $('.navbar').addClass("sticky");
                } else {
                    $('.navbar').removeClass("sticky");
                }

                // scroll-up button show/hide script
                if (scrollY > 500) {
                    $('.scroll-up-btn').addClass("show");
                } else {
                    $('.scroll-up-btn').removeClass("show");
                }
            });

            // slide-up script
            $('.scroll-up-btn').click(function () {
                $('html').animate({ scrollTop: 0 });
                // removing smooth scroll on slide-up button click
                $('html').css("scrollBehavior", "auto");
            });

            $('.navbar .menu li a').click(function () {
                // applying again smooth scroll on menu items click
                $('html').css("scrollBehavior", "smooth");
            });

            // toggle menu/navbar script
            $('.menu-btn').click(function () {
                $('.navbar .menu').toggleClass("active");
                $('.menu-btn i').toggleClass("active");
            });

            // typing text animation script
            // Typed:any
            var typed = new Typed(".typing", {
                strings: ["Developer", "Web Designer", "Engineer", "Designer", "Freelancer"],
                typeSpeed: 100,
                backSpeed: 60,
                loop: true
            });

            var typed = new Typed(".typing-2", {
                strings: ["YouTuber", "Developer", "Blogger", "Designer", "Freelancer"],
                typeSpeed: 100,
                backSpeed: 60,
                loop: true
            });

            // owl carousel script
            $('.carousel').owlCarousel({
                margin: 20,
                loop: true,
                autoplayTimeOut: 2000,
                autoplayHoverPause: true,
                responsive: {
                    0:{
                        items: 1,
                        nav: false
                    },
                    600:{
                        items: 2,
                        nav: false
                    },
                    1000:{
                        items: 3,
                        nav: false
                    }
                }
            });
        });
    }

    hireMe() {
        console.log("Hire me func")
        this.notifierService.notify("info", "This section is in progress");
    }

    isReadMore = true

  showText() {
     this.isReadMore = !this.isReadMore
  }
}
